<template>
    <div>
      <div class="UserHead" v-if="isLogin">
        <img class="userImg" src="../../assets/default-avatar.png">
        <div class="userInfo">
          <div class="isPhone">{{userInfo.mobile}}</div>
          <div class="isVip">♕普通会员</div>
        </div>
      </div>

      <div class="UserHead" v-else @click="$router.push('/login')">
        <img class="userImg" src="../../assets/default-avatar.png">
        <div class="userInfo">
          <div class="isPhone">未登录，请先登录</div>
          <div class="goLogin">点击登录</div>
        </div>
      </div>

      <div class="userItem">
        <div class="userBalance">
          <div style="color: green;">{{userInfo.balance || 0}}</div>
          <div>我的余额</div>
        </div>
        <div class="userPoints">
          <div>0</div>
          <div>积分</div>
        </div>
        <div class="userCoupon">
          <div>0</div>
          <div>优惠券</div>
        </div>
        <div class="userPurse">
          <van-icon name="balance-pay" />
          <div>我的钱包</div>
        </div>
      </div>

      <div class="userOrder">
        <div class="orderLeft">我的订单</div>
        <div class="orderRight" @click="MyOrder">全部></div>
      </div>
      <div class="userServe">
       <div>我的服务</div>
        <div class="serveList">
          <div class="userAddress" @click="MyAddress">
            <van-icon name="location-o" size="30px" color="green"/>
            <div>收货地址</div>
          </div>
        </div>
      </div>
    <div class="loginOut">
      <button class="loginOutButton" @click="loginOut" v-if="isLogin">退出登录</button>
      <div v-else></div>
    </div>
    </div>
  </template>

<script>
import { getUserInfo } from '@/api/user'

export default {
  name: 'userIndex',
  async created () {
    if (this.isLogin) {
      const res = await getUserInfo()
      console.log(res)
      this.userInfo = res.data.userInfo
    }
  },
  data () {
    return {
      userInfo: []
    }
  },
  computed: {
    isLogin () {
      return this.$store.getters.token
    }
  },
  methods: {
    loginOut () {
      this.$dialog.confirm({
        title: '温馨提示',
        message: '您要退出登录吗'
      })
        .then(() => {
          this.$store.dispatch('user/loginOut')
          this.userInfo = []
        })
        .catch(() => {
        })
    },
    MyOrder () {
      if (this.isLogin) {
        this.$router.push('/myorder')
      } else {
        return 0
      }
    },
    MyAddress () {
      if (this.isLogin) {
        this.$router.push('/address')
      } else {
        return 0
      }
    }
  }
}
</script>

  <style lang="less" scoped>
  .UserHead{
    display: flex;
    padding-top: 40px;
    padding-bottom: 30px;
    padding-left: 20px;
    background-color: rgb(253, 245, 233);
    .goLogin{
      font-size: 15px;
      padding-bottom: 5px;
      color: green;
    }
    .userImg{
      width: 40px;
      height: 40px;
      border-radius: 50% ;
    }
    .userInfo{
      margin-left: 10px;
    }
    .isPhone{
      color: green;
      font-size: 17px;
      text-align: center;
      padding-bottom: 5px;
    }
    .isVip{
      width: 80px;
      background-color: rgb(66, 59, 59);
      color: white;
      text-align: center;
      border-radius: 5px;
      margin-top: 5px;
      font-size: 13px;
    }
  }
  .userItem{
    display: flex;
    width: 100%;
    margin-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid gray;
    div{
      width: 100%;
      text-align: center;
      margin-bottom: 3px;
    }
  }
  .userOrder{
    display: flex;
    margin-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid gray;
    .orderLeft{
      margin-right: auto;
      margin-left: 10px;
    }
    .orderRight{
      margin-left: auto;
      margin-right: 10px;
      color: gray;
    }
  }
  .userServe{
    margin-top: 15px;
    margin-left: 10px;
    .serveList{
      display: flex;
      flex-wrap: wrap;
      .userAddress{
        width: 25%;
        margin-top: 10px;
        text-align: center;
      }
    }
  }
  .loginOut{
    display: flex;
    justify-content: center;
    margin-top: 30px;
    .loginOutButton{
      width: 200px;
      height: 40px;
      border: none;
      background-color: red;
      border-radius: 10px;
      color: white;
    }
  }
  </style>
