import { changeCount, getCartList, delGoods } from '@/api/cart'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCart (state, NewCartList) {
      state.cartList = NewCartList
    },
    changeChecked (state, goodsId) {
      const goodsCheck = state.cartList.find(item => item.goods_id === goodsId)
      goodsCheck.isChecked = !goodsCheck.isChecked
    },
    handleAllChecked (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCountMutations (state, { id, num }) {
      const goods = state.cartList.find(item => item.goods_id === id)
      goods.goods_num = num
    }
  },
  actions: {
    async getCartActions (context) {
      const res = await getCartList()
      console.log(res)

      const CartList = res.data.list
      CartList.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCart', CartList)
    },
    async changeCountAction (context, { id, num, skuId }) {
      const res = await changeCount(id, num, skuId)
      if (res) {
        context.commit('changeCountMutations', {
          id,
          num
        })
      }
    },
    async DelClickActions (context) {
      const DelData = context.getters.selCartList
      if (DelData.length === 0) {
        return 0
      } else {
        const DelId = DelData.map(item => item.id)
        await delGoods(DelId)
        await context.dispatch('getCartActions')
      }
    }
  },
  getters: {
    AllCart (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    CartPrice (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + (item.goods.goods_price_min * item.goods_num), 0).toFixed(2)
    },
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    BuyProduct (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    changeAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
