<template>
  <div class="count-box">
    <button class="minus" @click="handleMinus">-</button>
    <input :value="value" class="inp" type="text" @change="handleInp">
    <button class="add" @click="handleAdd">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    handleMinus () {
      if (this.value > 1) { this.$emit('input', this.value - 1) }
    },
    handleAdd () {
      this.$emit('input', this.value + 1)
    },
    handleInp (e) {
      const num = +e.target.value
      if (isNaN(num) || num <= 0) {
        e.target.value = this.value
      } else {
        this.$emit('input', num)
      }
    }
  }
}
</script>

<style lang="less" scoped>
    .count-box{
        width: 110px;
        display: flex;
        .minus , .add{
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            border-radius: 10px;
        }
        .inp{
            width: 40px;
            background-color: #efefef;
            outline: none;
            border: none;
            margin: 0 5px;
            text-align: center;
            border-radius: 10px;
        }
    }
</style>
