import { getItem, setItem } from '@/utils/storage'

export default {
  namespaced: true,
  state () {
    return {
      userInfo: getItem()
    }
  },
  mutations: {
    getUserInfo (state, obj) {
      state.userInfo = obj
      setItem(obj)
    }
  },
  actions: {
    loginOut (context) {
      context.commit('getUserInfo', {})
      context.commit('cart/setCart', [], { root: true })
    }
  },
  getters: {
  }
}
