const UserInfo = 'user-info-key'
const HistoryInfo = 'history-search'

export const getItem = () => {
  const res = localStorage.getItem(UserInfo)
  return res ? JSON.parse(res) : { token: '', userId: '' }
}
export const setItem = (info) => {
  localStorage.setItem(UserInfo, JSON.stringify(info))
}
export const removeItem = () => {
  localStorage.removeItem(UserInfo)
}
export const getHistoryItem = () => {
  const res = localStorage.getItem(HistoryInfo)
  return res ? JSON.parse(res) : []
}
export const setHistoryItem = (history) => {
  localStorage.setItem(HistoryInfo, JSON.stringify(history))
}
