<template>
  <div class="container">
    <div class="nevBar">
        购物车
      </div>

  <div v-if="cartList.length > 0">
    <div class="cartHead">
      <span class="left">共{{AllCart}}件商品</span>
      <span class="right" @click="isEdit = !isEdit" >
        <van-icon name="edit"/>
          编辑
      </span>
    </div>
    <div class="cartBody" v-for="item in cartList" :key="item.goods_id">
      <div  class="checked">
      <van-checkbox :value="item.isChecked" @click="handleChecked(item.goods_id)"></van-checkbox>
      </div>
      <img :src="item.goods.goods_image" class="bodyImg">
       <div class="productInfo">
        <div class="bodyTitle">{{item.goods.goods_name}}</div>
        <div class="priceAndCount">
          <div class="bodyPrice">￥{{item.goods.goods_price_min}}</div>
           <CountBox @input="(value)=>handleCount(item.goods_id,value,item.goods_sku_id)" class="bodyCount" :value="item.goods_num"></CountBox>
        </div>
      </div>
    </div>

    <!-- 购物车底部 -->
     <div class="cartFoot">
      <van-checkbox class="FootChecked" @click="handleAllChecked" :value="changeAllChecked">全选</van-checkbox>
      <span>合计:</span><div class="AllPrice">￥{{CartPrice}}</div>
      <button v-if="isEdit" class="FootButton" @click="goPay()">结算({{BuyProduct}})</button>
      <button v-else class="FootButton" @click="delClick()">删除</button>
      </div>
  </div>
     <div class="NullCart" v-else>
        <img class="NullCartImg" src="../../assets/empty.png">
        <span class="NullCartWord">购物车为空，快去逛逛吧！</span>
        <button class="NullCartButton" @click="$router.push('/')">去逛逛</button>
     </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'cartIndex',
  data () {
    return {
      isEdit: true
    }
  },
  components: {
    CountBox
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['AllCart', 'CartPrice', 'BuyProduct', 'changeAllChecked', 'selCartList'])
  },
  created () {
    if (this.$store.getters.token) {
      this.$store.dispatch('cart/getCartActions')
      this.$toast('删除成功')
    }
  },
  methods: {
    handleChecked (goodsId) {
      this.$store.commit('cart/changeChecked', goodsId)
    },
    handleAllChecked () {
      this.$store.commit('cart/handleAllChecked', !this.changeAllChecked)
    },
    handleCount (id, num, skuId) {
      this.$store.dispatch('cart/changeCountAction', {
        id,
        num,
        skuId
      })
    },
    async delClick () {
      if (this.selCountList === 0) {
        return 0
      } else {
        await this.$store.dispatch('cart/DelClickActions')
        this.isEdit = !this.isEdit
      }
    },
    goPay () {
      if (this.BuyProduct > 0) {
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.selCartList.map(item => item.id).join(',')
          }
        })
      }
    }
  },
  watch: {
    isEdit (value) {
      if (value) {
        this.$store.commit('cart/handleAllChecked', true)
      } else {
        this.$store.commit('cart/handleAllChecked', false)
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .container{
    background-color: rgb(244, 244, 244);
  }
  .nevBar{
      display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(255, 255, 255);
    height: 45px;
    font-size: larger;
    color: black;
    font-weight:300;
    top: 0;
    z-index: 10;
    position: sticky
    }
    .cartHead{
      display: flex;
      padding-top: 10px;
      .left{
        margin-right: auto;
        margin-left: 10px;
      }
      .right{
        margin-left: auto;
        margin-right: 10px;
      }
    }
    .cartBody{
      padding: 10px 10px;
      background-color: white;
      display: flex;
      margin: 10px 20px;
      border-radius: 10px;
      margin-bottom: 10px;
    }
    .productInfo{
      margin-left: 10px;
    }
    .checked{
      display: flex;
      justify-items: center;
    }
    .bodyImg{
      height: 90px;
    }
    .priceAndCount{
      display: flex;
      margin-top: 25px;
    }
    .bodyTitle{
      text-align: center;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .bodyPrice{
      margin-right: auto;
      margin-top: 10px;
      color: rgb(168, 81, 62);
      font-weight: 600;
    }
    .bodyCount{
      margin-left: auto;
    }
    .cartFoot{
      background-color: white;
      display: flex;
      align-items: center;
      position: fixed;
        bottom: 50px;
        left: 0; /* 如果需要让元素靠屏幕左侧对齐 */
        width: 100%;
      }
      .FootChecked{
        display: flex;
        margin-right: auto;
      }
      .FootButton{
        background-color: rgb(253, 63, 31);
        height: 40px;
        border-radius: 10px;
        border: none;
        margin-right: 8px;
        color: white;
        padding: 9px 9px;
      }
      .AllPrice{
        color: red;
        margin-right: 8px;
      }
      .NullCart{
        height: 384px;
        display: flex;
        flex-direction: column;
        // justify-content: center;
        align-items: center;
        overflow: hidden;
        .NullCartImg{
          margin-top: 70px;
          width: 150px;
        }
        .NullCartWord{
          margin-top: 20px;
          color: gray;
        }
        .NullCartButton{
          margin-top: 20px;
            border: none;
            width: 100px;
            height: 25px;
            color: white;
            background-color:red;
            border-radius: 10px;
          }
      }
</style>
