<template>
    <div>
      <van-nav-bar
        title="全部分类"
        left-arrow
        @click-left="$router.go(-1)"
      />
      <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          input-align="center"
          shape="round"
          @click="$router.push('/search')"
      />
      <div  class="bigBox">
        <div class="leftBody">
          <div class="cartBody" v-for="item in sideBar" :key="item.category_id" @click="toggleLeft(item.sort)">
            <div :class="{leftCartItem : true,active : item.sort === activeIndex}" >{{item.name}}</div>
          </div>
        </div>
        <div class="cateBody">
          <div class="cateItem" v-for="item in sideBarChildren" :key="item.category_id" @click="BodyClick(item.category_id)">
            <img class="cateImg" :src="item.image.external_url">{{item.name}}
          </div>
        </div>
      </div>
    </div>
  </template>

<script>
import { getCategory } from '@/api/category'
export default {
  name: 'categoryIndex',
  data () {
    return {
      value: '',
      sideBar: [],
      activeIndex: 1,
      sideBarChildren: [],
      sort: 1
    }
  },
  created () {
    this.getCategory()
  },
  methods: {
    async getCategory () {
      const res = await getCategory()
      this.sideBar = res.data.list
      this.sort = res.data.list.sort
      this.sideBarChildren = res.data.list[this.activeIndex - 1].children
    },
    toggleLeft (sort) {
      this.activeIndex = sort
      this.getCategory()
    },
    BodyClick (val) {
      this.$router.push(`/searchlist?categoryId=${val}`)
    }
  }
}
</script>

  <style scoped>
  .bigBox{
    position: fixed;
    width: 100%;
    height: 78%;
    display: flex;
  }
  .leftBody{
    width: 22%;
    overflow-y: scroll;
  }
  .cateBody{
    width: 78%;;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
  }
  .cateItem{
    font-size: 13px;
    display: flex;
    width: 33.3%;
    height: 120px;
    text-align: center;
    flex-direction: column;
  }
  .cateImg{
    max-width: 100%;
    height: 90px;
  }
  .leftCartItem{
    display: flex;
    background-color: rgb(243, 243, 243);
    height: 40px;
    justify-content: center;
    align-items: center;
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 17px;
  }
  .active{
    background-color: white;
    color: rgb(157, 121, 112);
  }
  </style>
